.board {
  background: rgba(22, 21, 21, 0.842);
  height: 32vw;
  width: 32vw;
  border-radius: 10px;
  color: #252b37;
  display: flex;
  justify-content: center;
  box-shadow: 0 38px 76px rgba(0,0,0,0.30), 0 30px 24px rgba(0,0,0,0.22);
}

.rows {
  line-height: 8.3vw;
  list-style: none;
  padding: 0;
  height: 28vw;
  width: 28vw;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
}

.tiles {
  font-weight: 900;
  text-align: center;
  float: left;
  padding: 0;
  height: 8.3vw;
  font-size: 4vw;
  width: 8.3vw;
  margin: 0.5vw;
  border-radius: 10px;
  background-color: #cacdd4;
  position: relative;
}

.tiles:hover {
  cursor: pointer;
}
